<template>
    <div>
        <Navbar/>
        <div class="content">
            <div class="container">
            <div class="row">
                <div class="col">
                    <div class="verify-title">申請資料已送出，請等候身份驗證</div>
                </div>
            </div>
            <div class="row mt-5">
                <div class="col-4"></div>
                <div class="col">
                    <b-form class="" @submit="onSubmit" @reset="onReset" v-if="show">
                        
                        <b-form-group id="input-group-4">
                            <b-form-radio-group
                                v-model="form.checked" 
                                id="checkboxes-4"
                                size="lg">
                            <b-form-radio name="radio-size" value="me">通過驗證後請通知我</b-form-radio>
                            </b-form-radio-group>
                        </b-form-group>

                        <b-form-group
                            id="input-group-1"
                            label=""
                            label-for="input-1"
                            description=""
                        >
                            <b-form-input
                            id="input-1"
                            v-model="form.email"
                            type="email"
                            required
                            placeholder="請輸入您的email"
                            ></b-form-input>
                        </b-form-group>
                        <b-button class="submit-btn w-50" size="sm" type="submit" variant="light">確認</b-button>
                        </b-form>
                </div>
                <div class="col-4"></div>
            </div>
            <div class="row mt-5">
                <div class="col">
                    <div class="verify-description">通過驗證後我們將寄送驗證成功通知與密碼給您</div>
                </div>
            </div>
            </div>
        </div>
    </div>
</template>
<script>
import Navbar from '@/components/Navbar/index'
export default {
    components:{
    Navbar
  },
  data() {
      return {
        form: {
          email: '',
          name: '',
          food: null,
          checked: []
        },
        foods: [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'],
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
}
</script>
<style scoped>
.verify-title{
    font-size: 2rem;
    font-weight: bold;
    color: #34DCA2;
}
.verify-description{
    /* color: darkgray; */
}
.submit-btn{
    background-color: #34DCA2;
}
.checkbox-form{
    font-size: 1.3rem;
}
</style>